<script setup lang="ts">
import dayjs from "dayjs";
import Api from "@/api"
/*表格*/
import {ref} from 'vue'
import {message} from 'ant-design-vue'
import type {VxeGridListeners} from 'vxe-table'
import useTicket from "@/composible/useTicket"
import {invalid} from "@/api/ticketController";

const ticketForm = ref({
  id: null,
  name: "",
  code: "",
  type: "",
  value: 0,
  valid_days: 5,
  start_date: "",
  expiry_date: "",
  usage_limit: 1,
  status: 1,
})
const onSubmit = async () => {
  await Api.ticketController.store(ticketForm.value)
}

const router = useRouter();

const {gridOptions} = useTicket()
const xGrid = ref()

// 编辑用户
const editCourse = (row: any) => {
  const grid = xGrid.value
  grid.setEditRow(row)
}
const qrClick = async () => {
  ticketForm.value.code = await Api.qrController.makeQr()
}
// 删除用户
const delCourse = async (row: any) => {
  try {
    /*    await destroy({id: row.id})*/
    xGrid.value.commitProxy('query')
  } catch (error) {
    message.error('删除失败')
  }
}
const addEvent = () => {
  router.push({path: `/event/form`})
}

const gridEvent: VxeGridListeners = {
  proxyQuery() {
    /*设置选项*/
    const column1 = xGrid.value.getColumnByField('course_type_id')
  },
}
/*作废*/
const InvalidTicket = async (ticket) => {
  await Api.ticketController.invalid({id: ticket.id},{status:ticket.status},{showMessage:true})
}
</script>

<template>
  <div>
    <a-divider>门票管理</a-divider>
    <a-row :gutter="[32,8]">
      <a-col :span="18">
        <vxe-grid
            ref="xGrid"
            v-bind="gridOptions"
            v-on="gridEvent"
        >
          <template #status="{ row }">
            <a-switch v-model:checked="row.status"
                      @change="InvalidTicket(row)"
                      :checkedValue="1"
                      :unCheckedValue="2"
                      checked-children="开"
                      un-checked-children="关" />
          </template>
        </vxe-grid>
      </a-col>

      <a-col :span="6">
        <a-card>
          <p>创建门票</p>
          <a-form :model="ticketForm">
            <a-form-item name="name" label="名称" :rules="[{ required: true, message: '必填' }]">
              <a-input v-model:value="ticketForm.name"></a-input>
            </a-form-item>
            <a-form-item name="code" label="编码" :rules="[{ required: true, message: '必填' }]">
              <a-input-group compact>
                <a-input v-model:value="ticketForm.code" style="width: calc(100% - 36px)"/>
                <a-tooltip title="点击生成">
                  <a-button @click="qrClick">
                    <template #icon>
                      <qrcode-outlined/>
                    </template>
                  </a-button>
                </a-tooltip>
              </a-input-group>
            </a-form-item>
            <a-form-item name="type" label="类型" :rules="[{ required: true, message: '必填' }]">
              <a-input v-model:value="ticketForm.type"></a-input>
            </a-form-item>
            <a-form-item name="value" label="面值" :rules="[{ required: true, message: '必填' }]">
              <a-input-number v-model:value="ticketForm.value">
                <template #addonAfter>元</template>
              </a-input-number>
            </a-form-item>
            <a-form-item name="valid_days" label="有效期（天）" :rules="[{ required: true, message: '必填' }]">
              <a-input-number v-model:value="ticketForm.valid_days"></a-input-number>
            </a-form-item>
            <a-form-item name="start_date" label="开始时间" :rules="[{ required: true, message: '必填' }]">
              <a-date-picker
                  v-model:value="ticketForm.start_date"
                  format="YYYY-MM-DD HH:mm:ss"
                  :show-time="{ defaultValue: dayjs('00:00:00', 'HH:mm:ss') }"
              />
            </a-form-item>
            <a-form-item name="expiry_date" label="过期时间" :rules="[{ required: true, message: '必填' }]">
              <a-date-picker
                  v-model:value="ticketForm.expiry_date"
                  format="YYYY-MM-DD HH:mm:ss"
                  :show-time="{ defaultValue: dayjs('00:00:00', 'HH:mm:ss') }"
              />
            </a-form-item>
            <a-form-item name="usage_limit" label="限制（次）" :rules="[{ required: true, message: '必填' }]">
              <a-input-number v-model:value="ticketForm.usage_limit"></a-input-number>
            </a-form-item>
            <a-form-item name="status" label="状态"
                         :rules="[{ required: true, message: '必填' }]">
              <a-radio-group v-model:value="ticketForm.status">
                <a-radio :value="1">可用</a-radio>
                <a-radio :value="2">下架</a-radio>
              </a-radio-group>
            </a-form-item>
            <a-form-item>
              <div class="steps-action">
                <a-button
                    type="primary"
                    html-type="submit"
                    @click="onSubmit"
                >
                  提交
                </a-button>
              </div>
            </a-form-item>

          </a-form>
        </a-card>
      </a-col>

    </a-row>
  </div>
</template>

<style scoped lang="less">

</style>